(function () {
    'use strict';

    angular
        .module('app', [
            'ngAnimate',
            'ui.bootstrap',
            'ngRoute',
            'toastr',
            'ngMessages'
        ]);

})();

(function () {
    'use strict';

    angular
        .module('app')
        .service('userEditDialog', function ($uibModal) {
            return function (user) {
                var modalInstance = $uibModal.open({
                    templateUrl: 'app/services/dialog/userEdit/user.edit.dialog.html',
                    controller: function ($scope) {
                        $scope.userModel = angular.copy(user);
                        $scope.ok = function () {
                            angular.equals(user, $scope.userModel) ?
                                modalInstance.close() :
                                modalInstance.close($scope.userModel);
                        };
                        $scope.cancel = function () {
                            modalInstance.dismiss('cancel');
                        };
                    }
                });
                return modalInstance.result;
            }
        });

})();

(function () {
    'use strict';

    angular
        .module('app')
        .service('confirmDialog', function ($uibModal) {
            return function (title, descr) {
                var modalInstance = $uibModal.open({
                    templateUrl: 'app/services/dialog/confirm/confirm.dialog.html',
                    controller: function ($scope) {
                        $scope.title = title;
                        $scope.descr = descr;
                        $scope.ok = function () {
                            modalInstance.close();
                        };
                        $scope.cancel = function () {
                            modalInstance.dismiss('cancel');
                        };
                    }
                });
                return modalInstance.result;
            }
        });

})();

(function () {
    'use strict';

    angular
        .module('app')
        .controller('usersGridCtrl', MainController);

    function MainController($scope, $filter, usersApi, confirmDialog) {

        $scope.limit = '10';
        $scope.currentPage = '1';
        $scope.ageRange = 'all';
        $scope.userModel = [];
        $scope.userDelete = userDelete;

        /**
         * Getting users data
         */


        usersApi.getAll().then(function (response) {
            $scope.userModel = response;
            filterUserList();
            $scope.loader = false;
        });

        /**
         * Watch for filter data
         */
        $scope.$watch('[currentPage, limit, sortReverse, searchTerm, ageRange]', function () {
            filterUserList();
        }, true);
        
        /**
         * Delete select user in dialog window
         * @param {Object} user - User data
         */
        function userDelete(user) {
            var title = 'Delete user';
            var descr = 'You really want to delete ' + user.firstName + ' ' + user.lastName;
            confirmDialog(title, descr)
                .then(function () {
                    usersApi.del(user.id).then(function (response) {
                        $scope.userModel = response;
                        filterUserList();
                    });
                });
        }
        
        /**
         * Filter for users
         * @returns {Array} {*} - Array with filtered user
         */
        function filterUserList() {
            var filteredData;
            if ($scope.ageRange) {
                var rangeArr = $scope.ageRange.split('-');
                rangeArr = rangeArr[1] ? rangeArr : $scope.ageRange.split('+');
                filteredData = $filter('rangeFilter')($scope.userModel, rangeArr[0], rangeArr[1]);
            }
            filteredData = $filter('filter')(filteredData, $scope.searchTerm);
            filteredData = $filter('orderBy')(filteredData, $scope.sortType, $scope.sortReverse);
            $scope.pagesLength = filteredData.length;
            filteredData = $filter('limitTo')(filteredData, $scope.limit, $scope.currentPage === 1 ? 0 : ($scope.currentPage - 1) * $scope.limit);
            $scope.userList = filteredData;
            return $scope.userList;
        }

    }
})();

(function () {
    'use strict';

    angular
        .module('app')
        .controller('userFormCtrl', MainController);

    function MainController($scope, $route, $window, $location, usersApi, confirmDialog) {

        var userModelCache = null;
        $scope.users = [];
        $scope.userModel = {};
        $scope.userAdd = userAdd;
        $scope.userEdit = userEdit;
        $scope.userDelete = userDelete;
        $scope.resetChanges = resetChanges;
        $scope.cancelForm = cancelForm;

        init();

        function init() {
            if ($route.current.params.id) {
                $scope.userId = $route.current.params.id;
                $scope.formTitle = 'Edit user';
                getUsersData();
            } else {
                $scope.formTitle = 'Add user';
                $scope.loader = false;
            }
        }

        /**
         * Getting users data by id
         */
        function getUsersData() {
            return usersApi.get($scope.userId).then(function (response) {
                $scope.userModel = response;
                userModelCache = angular.copy($scope.userModel);
                $scope.loader = false;
            });
        }


        /**
         * Save user
         * @param {Object} user - User data
         */
        function userEdit(user) {
            usersApi.put(user).then(function (response) {
                $scope.userModel = response;
                userModelCache = angular.copy($scope.userModel);
                $scope.userForm.$setPristine();
            });
        }

        /**
         * Add new user to users data
         * @param {Object} user - User data
         */
        function userAdd(user) {
            usersApi.post(user).then(function (response) {
                $location.path('/user/edit/' + response.id);
            });
        }

        /**
         * Delete select user with confirm dialog
         * @param {Object} user - User data
         */
        function userDelete(user) {
            var title = 'Delete user';
            var descr = 'You really want to delete ' + user.firstName + ' ' + user.lastName;
            confirmDialog(title, descr)
                .then(function () {
                    usersApi.del(user.id);
                    $window.history.back();
                });
        }

        /**
         * Reset changes
         */
        function resetChanges() {
            $scope.userModel = userModelCache;
        }

        /**
         * Exit from form
         */
        function cancelForm() {
            $window.history.back();
        }

    }
})();

